import { Fragment, useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { clearError, clearReviewDeleted } from "../../slices/productSlice";
import { deleteReview, getReviews } from "../../actions/productActions";
import Loader from "../layouts/Loader";
import { MDBDataTable } from "mdbreact";
import Sidebar from "./Sidebar";

export default function ReviewList() {
  const {
    reviews = [],
    loading = true,
    error,
    isReviewDeleted
  } = useSelector((state) => state.productState);
  const [productId, setProductId] = useState("");
  const dispatch = useDispatch();

  const setReviews = () => {
    const data = {
      columns: [
        {
          label: "ID",
          field: "id",
          sort: "asc",
        },
        {
          label: "Rating",
          field: "rating",
          sort: "asc",
        },
        {
          label: "User",
          field: "user",
          sort: "asc",
        },
        {
          label: "Comment",
          field: "comment",
          sort: "asc",
        },
        {
          label: "Actions",
          field: "actions",
          sort: "asc",
        },
      ],
      rows: [],
    };

    reviews.forEach((review) => {
      data.rows.push({
        id: review._id,
        rating: review.rating,
        user: review.user.name,
        comment: review.comment,
        actions: (
          <Fragment>
            <Button onClick={e => deleteHandler(e, review._id)} className="btn btn-danger py-1 px-2 ml-2">
              <i className="fa fa-trash"></i>
            </Button>
          </Fragment>
        ),
      });
    });
    return data;
  };

  //delete btn
  const deleteHandler = (e, id) => {
    e.target.disabled = true;
    dispatch(deleteReview(productId, id))
  }

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(getReviews(productId));
  }

  useEffect(() => {
    if (error) {
      toast(error, {
        position: "bottom-center",
        type: "error", //here set toast message box color error
        onOpen: () => {
          dispatch(clearError());
        }, //here we set the error val as null after the toast message shown so we don't get toast message every time open the login page after got error message
      });
      return;
    }

    if(isReviewDeleted) {
      toast('Review Deleted Successfully', {
          type: 'success',
          position: 'bottom-center',
          onOpen: () => { dispatch(clearReviewDeleted()) } //here we set the isUpdated val as false after the toast message shown so we don't get toast message every time open the updated page after got updated message
      })
      dispatch(getReviews(productId)) // here using this line to show the current other reviews after we deleted any reviews
      return;
    }
  }, [dispatch, error, isReviewDeleted]);

  return (
    <div className="row">
      <div className="col-12 col-md-2">
        <Sidebar />
      </div>
      <div className="col-12 col-md-10">
        <h1 className="my-4">Review List</h1>
        <div className="row justify-content-center mt-5">
            <div className="col-5">
                <form onSubmit={submitHandler}>
                    <div className="form-group">
                        <label>Product ID</label>
                        <input
                           type="text"
                           onChange={e => setProductId(e.target.value)}
                           value={productId}
                           className="form-control"
                        />
                    </div>
                    <button type="submit" disabled={loading} className="btn btn-primary btn-block py-2">
                        Search
                    </button>
                </form>
            </div>
        </div>
        <Fragment>
          {loading ? (
            <Loader />
          ) : (
            <MDBDataTable
              data={setReviews()}
              bordered
              striped
              hover
              className="px-3"
            />
          )}
        </Fragment>
      </div>
    </div>
  );
}
